.user-profile-left-user-img {
    position:absolute;
    margin-top: -15px;
    transform: translateY(-50%);
}

.user-profile-left-name {
    margin-top: 75px;
    margin-bottom: 25px;
}
.user-info-bio-text {
    display: flex;
    margin-bottom: 20px;
}
.user-infos-container {
    margin-bottom: 20px;
}
.user-infos-container  > *:not(:first-child) {
    margin-top: 8px;
}
.user-infos-container .user-infos-item {
    display: flex;
    align-items: center;

}
.user-infos-container .user-infos-item > *:not(:first-child) {
    margin-left: 10px;
   
}
.user-infos-item-icon {
    width: 20px;
    display: flex;
    justify-content: center;
}
p {
    padding: 30px 0px 20px 0px;
    text-align: justify;
}
.bio-social-media{
    padding-top: 25px;
}